/**
 * @module npm-js-web-oss
 * @desc web oss
 * @author swg [](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-oss-upload)
 */
import ajax from 'npm-js-ajax';
// import uuid from 'uuid';
const uuidv4 = require('uuid/v4');

let $script = null;
let fileName = '';
export default {
  /**
   * +
   * @param {obj} file 
   * @param {string} uploadPathPrefix 
   * @return {string}
   */
  getFileName (file, uploadPathPrefix) {
    let type = null;
    let uuid1 = uuidv4();
    // 1
    let arr = file.name.split('.');
    if (arr.length > 1) {
      type = arr[arr.length - 1];
    }

    // .xx
    type = type ? `.${type}` : '';
    return `${uploadPathPrefix}${uuid1}${type}`;
  },

  /**
   * oss client
   * wikihttp://wiki.sys.bandubanxie.com/pages/viewpage.action?pageId=2884647
   * @param {obj} param resourceTypepicaudiovideoplatformfeiosandroidcrmpurposexxx/xx_xx
   * @return {obj}
   */
  getSts (param) {
    return new Promise((resolve, reject) => {
      let response = ajax.send({
        interfaceType: -1,
        method: 'get',
        params: {
          resourceType: param.resourceType,
          platform: param.platform,
          purpose: param.purpose
        },
        withCredentials: true,
        headers: {},
        url: 'http://mobile.yunshuxie.com/v2/mobile/works_set/get/sts_oss_access_authority.json',
      });
      resolve(response);
    })
  },
  /**
   * scriptjsjs
   */
  addScript () {
    return new Promise((resolve, reject) => {
      $script = require('scriptjs');
      $script.get('https://cdn.yunshuxie.com/vendor/ali-oss/6.1.0/aliyun-oss-sdk.min.js', () => {
        resolve(true);
      });
    })
  },
  /**
   * oss
   * @param {obj} file 
   * @param {obj} param : getSts
   * @return {obj}
   */
  async initClient (file, param) {
    let sts = this.getSts(param);
    let script = null, promiseData = [];
    promiseData.push(sts);
    //js
    if(!document.querySelector("script[src='https://cdn.yunshuxie.com/vendor/ali-oss/6.1.0/aliyun-oss-sdk.min.js']")) {
      script = this.addScript();
      promiseData.push(script);
    }
    let ossClient = null;
    let obj = {};
    await Promise.all(promiseData).then((values) => {
      let response = values[0].data.data;
      fileName = this.getFileName(file, response.uploadPathPrefix);
      if(typeof response != 'string' && response.accessKeyId && response.accessKeySecret && response.securityToken) {
        ossClient = new OSS({
          region: 'oss-cn-beijing',
          bucket: 'ysx-sts-upload',
          // STS
          accessKeyId: response.accessKeyId,
          accessKeySecret: response.accessKeySecret,
          stsToken: response.securityToken,
          // endpoint: 'mobile.yunshuxie.com',
          // cname: true,
        });
      }
      obj = {
        ossClient: ossClient
      };
      let now = Date.now();
      if (Math.abs(response.nowTime - now) > 800000) {
        obj.flag = 0;
        obj.phoneNow = new Date(now).toLocaleString().replace(/:\d{1,2}$/,' ');
        obj.now = new Date(parseInt(response.nowTime)).toLocaleString().replace(/:\d{1,2}$/,' ');
      } else {
        obj.flag = 1;
      }
    }).catch((err) => {
      console.log(err);
    });
    return obj;
  },
  /**
   * 
   * @param {obj} file 
   * @param {obj} param : getSts
   * @return {string}
   */
  upload (file, param) {
    return new Promise((resolve, reject) => {
      this.initClient(file, param).then(obj => {
      if (!obj.flag) {
        let err = new Error(`${obj.now}${obj.phoneNow}`);
        reject(err);
      } else {
        obj.ossClient.put(`${fileName}`, file).then(function (result) {
          resolve(result.url);
        }).catch(function (err) {
          reject(err);
        });
      }
      }).catch(function (err) {
        reject(err);
      })
    })
  },
};
