/**
 * @module npm-js-number
 * @desc 
 * @author swg [](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-number)
 */
export default {
  /**
   * 0
   * @param {Number|String} value 0
   * @param {Number} digit 0
   * @return {String}
   */
  prefixZero (value = '', totalDigit = 1) {
    // 
    if (totalDigit <= value.toString().length) {
      return value;
    }

    // 0
    let preZeroNum = totalDigit - value.toString().length;

    // 0
    let prefix = [];
    for (let i = 0; i < preZeroNum; i++) {
      prefix.push('0');
    }

    // 0+
    return prefix.join('') + value;
  },

  /**
   * -
   * @param {Number} a a
   * @param {Number} b b
   * @param {Number} digit 
   * @returns {Number}
   */
  plus (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) + parseInt(b * expand)) / expand;
  },

  /**
   * -
   * @param {Number} a a
   * @param {Number} b b
   * @param {Number} digit 
   * @returns {Number}
   */
  minus (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) - parseInt(b * expand)) / expand;
  },

  /**
   * -
   * @param {Number} a a
   * @param {Number} b b
   * @param {Number} digit 
   * @returns {Number}
   */
  multiply (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) * parseInt(b * expand)) / expand;
  },

  /**
   * -
   * @param {Number} a a
   * @param {Number} b b
   * @param {Number} digit 
   * @returns {Number}
   */
  divide (a = 0, b = 0, digit = 2) {
    let expand = Math.pow(10, digit);
    return (parseInt(a * expand) / parseInt(b * expand)) / expand;
  },
};
