/**
 * @module dvd-base-js-type
 * @desc 
 * @author swg [](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-type)
 */
export default {
  /**
   * Number
   * @param {Object} value 
   * @return {boolean}
   */
  isNumber (value) {
    return Object.prototype.toString.call(value) === '[object Number]';
  },

  /**
   * String
   * @param {Object} value 
   * @return {boolean}
   */
  isString (value) {
    return Object.prototype.toString.call(value) === '[object String]';
  },

  /**
   * Boolean
   * @param {Object} value 
   * @return {boolean}
   */
  isBoolean (value) {
    return Object.prototype.toString.call(value) === '[object Boolean]';
  },

  /**
   * Object
   * @param {Object} value 
   * @return {boolean}
   */
  isObject (value) {
    return Object.prototype.toString.call(value) === '[object Object]';
  },

  /**
   * Array
   * @param {Object} value 
   * @return {boolean}
   */
  isArray (value) {
    return Object.prototype.toString.call(value) === '[object Array]';
  },

  /**
   * Function
   * @param {Object} value 
   * @return {boolean}
   */
  isFunction (value) {
    return Object.prototype.toString.call(value) === '[object Function]';
  },
};
