/**
 * @module npm-js-runtime
 * @desc 
 * @author swg [](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-runtime)
 */
export default {
  /**
   * serverserverNodeJS
   * @returns {Boolean}
   */
  isServer () {
    return typeof global !== 'undefined' && typeof global.process !== 'undefined';
  },

  /**
   * clientclientapp
   * @returns {Boolean}
   */
  isClient () {
    return !this.isServer();
  },

  /**
   * Linux
   * @returns {Boolean}
   */
  isLinux () {
    return typeof global !== 'undefined' && global.process && global.process.platform === 'linux';
  },
};
