import type from 'npm-js-type';
import number from 'npm-js-number';

/**
 * @module npm-js-time
 * @desc 
 * @author swg [](http://gitlab.sys.bandubanxie.com/fe/npm/tree/master/npm-js-time)
 */
export default {
  /**
   * 
   * @param {Date|Number|String} date ||
   * @param {String} format 
   * @returns {String}
   */
  format (date = new Date(), format = 'yyyy-MM-dd hh:mm:ss SSS') {
    // dateDate
    if (type.isString(date)) {
      date = new Date(parseInt(date));
    } else if (type.isNumber(date)) {
      date = new Date(date);
    }

    // format -> 
    let map = {
      y: date.getFullYear(),
      M: date.getMonth() + 1,
      d: date.getDate(),
      h: date.getHours(),
      m: date.getMinutes(),
      s: date.getSeconds(),
      S: date.getMilliseconds(),
    };

    // 
    let value = format;
    for (let key in map) {
      value = value.replace(new RegExp(`${key}+`), (matchValue, index, input) => {
        return number.prefixZero(map[key], matchValue.length);
      });
    }

    return value;
  },

  /**
   * 
   * @param {Number} year 
   * @param {Number} month 1-12
   * @return {Number}
   */
  getDayCount (year = new Date().getFullYear(), month = new Date().getMonth() + 1) {
    let days = null;

    // 30
    if (month === 4 || month === 6 || month === 9 || month === 11) {
      days = 30;

      // 2
    } else if (month === 2) {
      // 282
      days = 28;

      // 292
      if (year % 4 === 0) {
        days += 1;
      }

      // 31
    } else {
      days = 31;
    }

    return days;
  },

  /**
   * 
   * @param {Date} options.targetDate Date
   * @param {Number} options.targetTimestamp 
   * @param {Number} options.second targetDatetargetTimestamp
   * @param {Number} options.minute targetDatetargetTimestamp
   * @param {Number} options.hour targetDatetargetTimestamp
   * @param {Number} options.day targetDatetargetTimestamp
   * @param {Number} options.digit 19:119:01
   * @returns {Object} {{second: Number, minute: Number, hour: Number, day: Number}}
   */
  getTimeDuration (options = {}) {
    // 
    options.second = options.second || 0;
    options.minute = options.minute || 0;
    options.hour = options.hour || 0;
    options.day = options.day || 0;

    // 
    let second = 1000;
    let minute = 60000;
    let hour = 3600000;
    let day = 86400000;

    // 
    let distance = 0;
    if (options.targetDate) {
      distance = options.targetDate - new Date();
    } else if (options.targetTimestamp) {
      distance = new Date(options.targetTimestamp) - new Date();
    } else {
      distance = options.second * second + options.minute * minute + options.hour * hour + options.day * day;
    }

    let result = {
      second: parseInt(distance % minute / second),
      minute: parseInt(distance % hour / minute),
      hour: parseInt(distance % day / hour),
      day: parseInt(distance / day),
    };

    // 
    return {
      second: options.digit ? number.prefixZero(result.second, options.digit) : result.second,
      minute: options.digit ? number.prefixZero(result.minute, options.digit) : result.minute,
      hour: options.digit ? number.prefixZero(result.hour, options.digit) : result.hour,
      day: options.digit ? number.prefixZero(result.day, options.digit) : result.day,
    };
  },
};
