export default {
  name: 'NpmComBgDialog',
  data () {
    return {
      rendered: false,
    };
  },

  mounted () {
    if (this.visible) {
      this.rendered = true;
      this.open();
    }
  },

  watch: {
    visible(val) {
      if (val) {
        this.close = false;
        this.$emit('open');
        this.$nextTick(() => {
          this.$refs.dialog.scrollTop = 0;
        });
      } else {
        if (!this.close) {
          this.$emit('close');
        }
      }
    },
  },

  computed: {
    style() {
      let style = {};
      if (!this.fullscreen) {
        style.marginTop = this.top;
        if (this.width) {
          style.width = this.width;
        }
      }
      return style;
    },
  },

  props: {
    /** 
     * 
    */
    title: {
      type: String,
      default: '',
    },
    /** 
     * 
    */
    visible: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    closeOnClickModal: {
      type: Boolean,
      default: true
    },
    /** 
     * 
    */
    width: String,
    /** 
     * 
    */
    fullscreen: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    top: {
      type: String,
      default: '15vh',
    },
    /** 
     * 
    */
    beforeClose: Function,
    /** 
     * 
    */
    center: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    customClass: {
      type: String,
      default: ''
    },
    /** 
     * 
    */
    showClose: {
      type: Boolean,
      default: true
    },
  },

  methods: {
    handleClose () {
      if (this.beforeClose) {
        this.beforeClose();
      } else {
        this.hide();
      }
    },

    hide (cancel) {
      if (cancel !== false) {
        this.$emit('update:visible', false);
        this.$emit('close');
        this.closed = true;
      }
    },

    handleWrapperClick () {
      if (!this.closeOnClickModal) return;
      this.handleClose();
    },

    afterEnter () {
      this.$emit('opened');
    },

    afterLeave () {
      this.$emit('closed');
    },

    destroyed () {
      if (this.appendToBody && this.$el && this.$el.parentNode) {
        this.$el.parentNode.removeChild(this.$el);
      }
    }
  },
};
